.deptContainer{
        margin: 3% auto;
        width: 70%;
        text-align: center;        
}

.deptContainer li {
        background-color: #96897B;
        padding: 2%;
        font-size: 1.25em;
        margin: 4%;
        color: #fff;
        cursor: pointer;
        text-shadow: 1px 1px 2px #616161;
        margin: 2%;
        display: inline-block;
    }

    .deptContainer li:nth-child(1) {
        background-color: #9E788F;
    }

    .deptContainer li:nth-child(2) {
        background-color: #A5C4D4;
    }

    .deptContainer li:nth-child(3) {
        background-color: #99C2A2;
    }

    .deptContainer li:hover {
        background-color: #C2E812;
    }

    .cardImg{
        width: 70%;
        margin: 15% auto;
    }

    .card{
        -webkit-transition: all .2s ease-in-out;
        -o-transition: all .2s ease-in-out;
        transition: all .2s ease-in-out;
          -webkit-animation-name: fadeInUp;
                  animation-name: fadeInUp;
    }
    .card:hover{
        -webkit-transform: scale(1.025);
            -ms-transform: scale(1.025);
                transform: scale(1.025);
    }
@-webkit-keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: none;
            transform: none;
  }
}




